export default {
    // 
    isvalidUsername(str) {
        const valid_map = ['admin', 'table', 'nested']
        return valid_map.indexOf(str.trim()) >= 0
    },
    isExternal(path) {
        return /^(https?:|mailto:|tel:)/.test(path)
    },

    // 
    /**
     * 
     * 2~12,
     * @param {*} rule 
     * @param {*} value 
     * @param {*} callback 
     * */
    validateSign(rule, value, callback) {
        const reg = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z0-9]{1,11}$/
        if (value) {
            if (!reg.test(value)) {
                callback(new Error("'2-12'"));
            } else {
                callback();
            }
        } else {
            callback(new Error(""));
        }
    },

    /**
     * 
     * [],   
     * @param {*} rule 
     * @param {*} value 
     * @param {*} callback 
     * */
    validateModel(rule, value, callback) {
        const reg = /[[]]/
        if (value) {
            if (reg.test(value)) {
                callback(new Error("[],   "));
            } else {
                callback();
            }
        } else {
            callback(new Error(""));
        }
    },

    /**
     * 
     * @param {*} rule 
     * @param {*} value 
     * @param {*} callback 
     */
    specialCharacters(rule, value, callback) {
        const reg = /[`~!@#$%^&*()_\-+=<>?:"{}|,.\/;'\\[\]~@#%&*\-+={}|]/im
        if (value) {
            if (!reg.test(value)) {
                callback(new Error(""));
            } else {
                callback();
            }
        } else {
            callback(new Error(""));
        }
    },

    /**
     * 
     * @param {*} rule 
     * @param {*} value 
     * @param {*} callback 
     */
    validateAlipay(rule, value, callback) {
        const reg = /^(?:\w+\.?)*\w+@(?:\w+\.)+\w+|\d{9,11}$/;
        if (rule.required) {
            if (!value) {
                callback(new Error(''));
            } else if (!reg.test(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
        } else if (value && !reg.test(value)) {
            callback(new Error(''));
        } else {
            callback();
        }
    },

    /**
     * 
     * @param {*} rule 
     * @param {*} value 
     * @param {*} callback 
     */
    EnglishAndNum(rule, value, cb) {
        const reg = new RegExp('^[a-zA-Z0-9]+$');
        if (rule.required) {
            if (!value) {
                cb(new Error(''));
            } else if (!reg.test(value)) {
                cb(new Error(''));
            } else {
                cb();
            }
        } else if (value && !reg.test(value)) {
            cb(new Error(''));
        } else {
            cb();
        }
    },

    /**
     * 
     * @param {*} rule 
     * @param {*} value 
     * @param {*} callback 
     */
    positiveInteger(rule, value, cb) {
        const reg = new RegExp('^[0-9]*$');
        if (rule.required) {
            if (!value) {
                cb(new Error(''));
            } else if (!reg.test(value)) {
                cb(new Error(''));
            } else {
                cb();
            }
        } else if (value && !reg.test(value)) {
            cb(new Error(''));
        } else {
            cb();
        }
    },

    /**
     *  
     * @param {*} rule 
     * @param {*} value 
     * @param {*} callback 
     */
    validateNumber(rule, value, cb) {
        const reg = new RegExp('^[+]?[0-9]*\\.?[0-9]+$');
        if (rule.required) {
            if (!value && Number(value) !== 0) {
                cb(new Error(''));
            } else if (!reg.test(value)) {
                cb(new Error(''));
            } else {
                cb();
            }
        } else if (value && !reg.test(value)) {
            cb(new Error(''));
        } else {
            cb();
        }
    },

    /**
     * 
     * @param {*} rule 
     * @param {*} value 
     * @param {*} callback 
     */
    validateEmail(rule, value, callback) {
        const reg = /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/;
        if (rule.required) {
            if (!value) {
                callback(new Error(''));
            } else if (!reg.test(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
        } else if (value && !reg.test(value)) {
            callback(new Error(''));
        } else {
            callback();
        }
    },

    /**
     * 
     * @param {*} rule 
     * @param {*} value 
     * @param {*} callback 
     */
    validatePhone(rule, value, callback) {
        const reg = /^1[3|4|5|6|7|8][0-9]\d{8}$/;
        if (rule.required) {
            if (!value) {
                callback(new Error(''));
            } else if (!reg.test(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
        } else if (value && !reg.test(value)) {
            callback(new Error(''));
        } else {
            callback();
        }
    },

    /**
     * 
     * @param {*} rule 
     * @param {*} value 
     * @param {*} callback 
     */
    validatePassword(rule, value, callback) {
        const hasUpperCase = /[A-Z]+?/.test(value);
        const hasLowerCase = /[a-z]+?/.test(value);
        const hasNumber = /[0-9]+?/.test(value);
        const hasSpecial = /[_.#?!@$%^&*-]+?/.test(value);
        const pass = [hasUpperCase, hasLowerCase, hasNumber, hasSpecial].filter(
            item => item
        );
        const errMsg = '_.#?!@$%^&*-';
        if (rule.required) {
            if (!value) {
                callback(new Error(''));
            } else if (value.length < 6) {
                callback(new Error('6'));
            } else if (pass.length < 2) {
                callback(errMsg);
            } else {
                callback();
            }
        } else if (value) {
            if (value.length < 6) {
                callback(new Error('6'));
            } else if (pass.length < 2) {
                callback(errMsg);
            }
        } else {
            callback();
        }
    },






}